@font-face {
  font-family: figbold;
  src: url("Figtree-ExtraBold.73941940.ttf") format("truetype");
  font-weight: 800;
}

@font-face {
  font-family: fig;
  src: url("Figtree-SemiBold.1f5362b8.ttf") format("truetype");
  font-weight: 500;
}

:root {
  --prim: #f4d04e;
  --white: #fff;
  --grey: gray;
  --black: #121212;
}

*, :after, :before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
}

body {
  font-family: fig, Courier New, Courier, monospace;
}

.main-content {
  background-color: var(--prim);
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  display: flex;
}

.card {
  background-color: var(--white);
  box-shadow: 5px 5px 0 var(--black);
  border: 1.5px solid var(--black);
  border-radius: 1.5rem;
  width: 30rem;
  padding: 2rem;
  transition: all .3s cubic-bezier(.55, .085, .68, .53);
}

.card:hover {
  box-shadow: 10px 10px 0 var(--black);
}

.card__header {
  border-radius: 1.5rem;
  width: 100%;
  margin-bottom: 2rem;
  overflow: hidden;
}

.card__img {
  object-fit: cover;
  width: 100%;
}

.btn {
  background-color: var(--prim);
  border: none;
  border-radius: 5px;
  margin-bottom: 1rem;
  padding: .4rem 1rem;
  font-family: figbold;
  font-size: 1.2rem;
}

.card__publish-info {
  margin-bottom: 2rem;
  font-size: 1.2rem;
}

.card__heading {
  cursor: pointer;
  margin-bottom: 2rem;
  font-family: figbold;
  font-size: 1.9rem;
  transition: all .3s cubic-bezier(.175, .885, .32, 1.275);
}

.card__heading:hover {
  color: var(--prim);
}

.card__info {
  color: var(--grey);
  margin-bottom: 2rem;
  font-family: fig;
  font-size: 1.2rem;
  line-height: 1.6;
}

.card__footer {
  align-items: center;
  display: flex;
}

.card__user-img {
  width: 3rem;
  margin-right: 1rem;
}

.card__user-img-caption {
  font-family: figbold;
  font-size: 1.1rem;
}

@media screen and (width <= 375px) {
  html {
    font-size: 50%;
  }
}
